export type FSStateType =  {
    [key: string]: any
    open_files: Array<{file_name: string}>
}

const InitFSState: FSStateType = {
    open_files: []
}

export enum UIActionTypes {
    openFile = 'OPEN_FILE',
    closeFile = 'CLOSE_FILE',
}

export interface FSAction {
    type: UIActionTypes
    payload: any
}

const FSReducer = (state = InitFSState, action: FSAction) => {
    switch (action.type) {
        case UIActionTypes.openFile:
            break;
        case UIActionTypes.closeFile:
            break;

        default:
            return state
    }
}

export default  FSReducer
