import React, {ReactNode} from 'react'
import {Breadcrumb, Button, Col, Divider, Drawer, Empty, notification, PageHeader, Row, Statistic, Tabs, Tag, Tooltip} from "antd";
import {SmileOutlined, GithubOutlined, GitlabOutlined} from "@ant-design/icons";
import {useDispatch, useSelector} from "react-redux";
import {UIStateType} from "../reducers/UIReducer";
import store from "../store";
import {toggleSidebar} from "../actions/UIAction";

import './Project.css'

const {TabPane} = Tabs

const Project: React.FC<{}> = (props) => {
    const dispatch = useDispatch<typeof store.dispatch>()

    const [state, setState] = React.useState({ visible: false });

    const showDrawer = () => {
        setState({
            visible: true,
        })
    };

    const onClose = () => {
        setState({
            visible: false,
        });
    };

    // TODO:Add browser router for page navigation

    return (
        <>

            <PageHeader
                // onBack={() => window.history.back()}
                title="Projects"
                // tags={<Tag color="blue" onClick={showDrawer}>Running</Tag>}
                subTitle="This is a subtitle"
                extra={[
                    <Tooltip title="GitHub">
                        <Button key="3" href='//github.com/deveshpankaj' type='text'  target='_blank'><GithubOutlined style={{color: '#7DBBE6'}} /></Button>
                    </Tooltip>,
                    <Tooltip title="GitLab">
                        <Button key="2" href='//gitlab.com/deveshpankaj' type='text' target='_blank'><GitlabOutlined style={{color: ' #FC6D27'}} /></Button>
                    </Tooltip>,
                    // <Button key="1" type="primary" onClick={()=>dispatch(toggleSidebar())}>Private</Button>,
                ]}

                footer={
                    <Tabs defaultActiveKey="4">
                        <TabPane tab={<Tooltip mouseEnterDelay={1} title="Current Public Working Projects">Working</Tooltip>} key="4">
                            <div style={{marginTop: '1rem'}}>
                                <Working/>
                            </div>
                        </TabPane>
                        <TabPane tab={<Tooltip mouseEnterDelay={2} title="Organisations">Organisations</Tooltip>} key="5">
                            <div style={{marginTop: '1rem'}}>
                                <Organisations/>
                            </div>
                        </TabPane>
                        <TabPane tab={<Tooltip mouseEnterDelay={3} title="Other">Other</Tooltip>} key="6">
                            <div style={{marginTop: '1rem'}}>
                                <Other/>
                            </div>
                        </TabPane>
                    </Tabs>
                }
            >
                <Row>
                    <Statistic 
                        title="Working" 
                        // prefix="$"
                        value={1}/>
                    <Statistic
                        title="Maintaining"
                        // prefix="$"
                        value={2}
                        style={{
                            margin: '0 32px',
                        }}
                    />
                    <Statistic 
                        title="Other" 
                        // prefix="$" 
                        value={3}/>
                </Row>


                
            </PageHeader>



            <Drawer
                title="Basic Drawer"
                placement="right"
                closable={false}
                onClose={onClose}
                visible={state.visible}
                getContainer={false}
                width={'70%'}
                style={{ position: 'absolute' }}
            >
                <p>Some contents...</p>
            </Drawer>
        </>
    )
}


const Working = () => {
    return (<div>
        <Divider orientation="left">Portfolio</Divider>
        <Empty />
    </div>)
}

const Organisations = () => {
    return (<div>
        <Divider orientation="left">AndroidJS</Divider>
        <Empty />
    </div>)
}

const Other = () => {
    return (<div style={{height: '100%'}}>
        <Divider orientation="left">Github Project 1</Divider>
        <Empty />

        <Divider orientation="left">Github Project 2</Divider>
        <Empty />
        <Divider orientation="left">Github Project 3</Divider>
        <Empty />
        <Divider orientation="left">Github Project 4</Divider>
        <Empty />
    </div>)
}



export default Project
