export type UIStateType =  {
    [key: string]: any
    sidebar: boolean
    currentPage: 'HOME'|'PROJECTS'|'CONTACT'|'GALLERY'
}

const InitUIState: UIStateType = {
    sidebar: false,
    currentPage: "PROJECTS"
}

export enum UIActionTypes {
    openSidebar = 'OPEN_SIDEBAR',
    closeSidebar = 'CLOSE_SIDEBAR',
    toggleSidebar = 'TOGGLE_SIDEBAR',

    currentPage = 'CURRENT_PAGE',
}

export interface UIAction {
    type: UIActionTypes
    payload: any
}

const UIReducer = (state = InitUIState, action: UIAction) => {
    switch (action.type) {
        case UIActionTypes.currentPage:
            return {...state, currentPage: action.payload}

        case UIActionTypes.toggleSidebar:
            return {...state, sidebar: !state.sidebar}

        case UIActionTypes.openSidebar:
            return {...state, sidebar: true}

        case UIActionTypes.closeSidebar:
            return {...state, sidebar: false}

        default:
            return state
    }
}

export default  UIReducer
