import React from 'react'
import {Badge, Card, Modal, notification, Skeleton, Space, Typography} from "antd";
import {SmileOutlined} from "@ant-design/icons";
import loadingMessages from '../utils/loading-message'
import {useDispatch, useSelector} from "react-redux";
import store from "../store";
import {setCurrentPage} from "../actions/UIAction";
import './SideNav.css'
import {UIStateType} from "../reducers/UIReducer";

const {Paragraph, Text, Title} = Typography
// const openNotification = () => {
//     notification.open({
//         message: 'Notification Title',
//         description:
//             'This is the content of the notification. This is the content of the notification. This is the content of the notification.',
//         icon: <SmileOutlined style={{color: '#108ee9'}}/>,
//     });
// };

const SideNav: React.FC<{}> = props => {
    const currentPage = useSelector((state: {ui:UIStateType}) => state.ui.currentPage)
    const dispatch = useDispatch<typeof store.dispatch>()

    return <>
        <div style={{display: 'flex', flexDirection: 'column', gap: '1rem'}} id={'side-nav'}>
            <Space direction="vertical" style={{width: '100%'}}>
                <Badge.Ribbon text="ABOUT">
                    <Card className={currentPage==='HOME'?'nav-item-card active' : 'nav-item-card'} id={'side-nav-about'} onClick={() => {
                        dispatch(setCurrentPage('HOME'));
                        // openNotification()
                    }}>Pankaj Devesh</Card>
                </Badge.Ribbon>
                <Badge.Ribbon text="PROJECTS" color="purple">
                    <Card className={currentPage==='PROJECTS'?'nav-item-card active' : 'nav-item-card'} onClick={() => dispatch(setCurrentPage('PROJECTS'))}>Software
                        Developer</Card>
                </Badge.Ribbon>
                <Badge.Ribbon text="CONTACT" color="#2db7f5">
                    <Card className={currentPage==='CONTACT'?'nav-item-card active' : 'nav-item-card'} onClick={() => dispatch(setCurrentPage('CONTACT'))}>From
                        India</Card>
                </Badge.Ribbon>
                <Badge.Ribbon text="GALLERY" color="#2db7f5" placement="start">
                    <Card className={currentPage==='GALLERY'?'nav-item-card active' : 'nav-item-card'} onClick={()=>{dispatch(setCurrentPage('GALLERY'))}}>The Journey</Card>
                </Badge.Ribbon>
            </Space>

            <div style={{padding: '1rem'}}>
                {/*<Image width={300} src={me} preview={false}/>*/}
            </div>
        </div>
    </>
}

export default SideNav
